import {  mapMutations } from 'vuex';
import { loginOut } from '@/api/login.js'
export default {
	data() {
		return {
			scrollTop: 0,
			isShowScan: false,
			showLoginOut: false,
		};
	},
	computed: {
		userInfo() {
			return this.$store.state.user.userInfo
		}
	},
	methods: {
		...mapMutations(['setLoginOut']),
		confirmOut() {
			this.showLoginOut = true
			this.$refs.loginOut.modalAction({
				confirm: res => {
					loginOut().then(res => {
						this.setLoginOut()
						uni.navigateTo({
							url: '/pages/login/login'
						})
					})
				}
			})
		},
	}
}
